import React from 'react'
import { Link } from 'gatsby'
import moment from 'moment'
import './style.scss'

class Post extends React.Component {
  render() {
    const {
      title,
      date,
      category,
      description,
      language,
    } = this.props.data.node.frontmatter
    const { slug, categorySlug } = this.props.data.node.fields
    const thumbnail = this.props.data.node.frontmatter.thumbnail
    const thumbnail_alt = this.props.data.node.frontmatter.thumbnail_alt

    const read_the_article = (language === 'en') ? 'Read the article' : 'Leia o artigo'

    return (
      <div className="post" lang={language}>
        <div className="post__meta">
          <time
            className="post__meta-time"
            dateTime={moment(date).format('MMMM D, YYYY')}
          >
            {moment(date).format('DD MMM YYYY')}
          </time>
          <span className="post__meta-divider" />
          <span className="post__meta-category" key={categorySlug}>
            <Link to={categorySlug} className="post__meta-category-link">
              {category}
            </Link>
          </span>
        </div>
        <h2 className="post__title">
          <Link className="post__title-link" to={slug}>
            {title}
          </Link>
        </h2>
        <p className="post__description">
          
          {description}
        </p>
        <Link className="post__readmore" to={slug}>
          {read_the_article}
        </Link>
      </div>
    )
  }
}

export default Post
